.simpleSlider .innerSlide {
  display: flex;
  justify-content: space-between;
  padding: 20px 0 40px 0;
}
.simpleSlider {
  margin-bottom: 150px;
}
.simpleSlider h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 80px;
  line-height: 100px;
  margin-top: 30px;
}
.simpleSlider p {
  font-style: normal;
  font-weight: 500;
  font-size: 25px;
  line-height: 130%;
}
.simpleSlider .everyWeekend {
  font-weight: bold;
}
.simpleSlider h2 + p {
  margin-top: 60px;
  margin-bottom: 30px;
}
.simpleSlider .bakeCoffee {
  margin-bottom: 90px;
}
.simpleSlider .linkScrollBtn {
  background-color: #f9b300;
  border: none;
  border-radius: 5px;
  padding: 22px 56px;
  color: white;
  font-style: normal;
  font-weight: bold;
  font-size: 30px;
  line-height: 36px;
  cursor: pointer;
}
.simpleSlider .linkScrollBtn:hover {
  background-color: #e2a300;
  text-decoration: none;
}
.simpleSlider .slick-dots {
  position: absolute;
  top: 0;
  right: 0;
  text-align: right;
  height: 25px;
}
.simpleSlider .slick-dots li {
  margin-left: 20px;
}
.simpleSlider .slick-dots li button:before {
  font-size: 20px;
  color: white;
  opacity: 1;
}
.simpleSlider .slick-dots .slick-active button:before {
  color: rgb(43, 17, 17);
}

