.alert {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  border: 1px solid #cccccc;
  border-radius: 10px;
  box-shadow: 0 0 10px #cccccc;
  width: 30%;
  animation: show 500ms ease-in-out;
  padding: 20px;
  z-index: 20;
  font-size: 16px;
}

@keyframes show {
  from {
    top: 50%;
    opacity: 0;
  }
  to {
    top: 50%;
    opacity: 1;
  }
}
.basketWidget {
  cursor: pointer;
}
.basketCard {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px 30px 30px 30px;
  border-radius: 10px;
  width: 60rem;
  max-width: 95%;
  box-shadow: -1px 1px 16px 3px rgba(13, 14, 15, 0.2);
  animation: show 500ms ease-in-out;
  z-index: 15;
}
@keyframes show {
  from {
    top: 70%;
    opacity: 0;
  }
  to {
    top: 50%;
    opacity: 1;
  }
}
